/*FORMS*/
form {
    width: 550px;
    min-height: 550px;
    height: auto;
    border-radius: 5px;
    margin: 2% auto;
    -webkit-box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.31);
            box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.31);
    padding: 2%;
}
/* form Container */
form .con {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;

      margin: 0 auto;
}

/* the header form form */
header {
    margin: 2% auto 20% auto;
    text-align: center;
}
/* Login title form form */
header h2 {
    font-size: 42px;
    color: #3e403f;
}
/*  A welcome message or an explanation of the login form */
header p {
  letter-spacing: 0.05em;
  font-size: 14px
}


